import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    works: [
      {
        id: 1,
        title: '机票预订申请',
        authorName: '李白',
        node: 1,
        time: '2019-03-25 19:24'
      },
      {
        id: 2,
        title: '机票预订申请',
        authorName: '李白',
        node: 2,
        time: '2019-03-25 19:25'
      },
    ],
    progress: ['组长审批', '项目经理审批', '中止', '拒绝'],
    tab: 'all',
    nav: 'agenda',
  },
  getters: {
    filterWorks: state => state.works.filter(item => state.tab === 'all' || +state.tab === +item.node),
    progress: state => state.progress,
    nav: state => state.nav,
  },
  mutations: {
    ['SET_TAB'](state, payload) {
      state.tab = payload.tab
    },
    ['SET_NAV'](state, payload) {
      state.nav = payload.nav
    },
  },
  actions: {
    setTab({ commit }, payload) {
      commit('SET_TAB', payload)
    },
    setNav({ commit }, payload) {
      commit('SET_NAV', payload)
    },
  }
})
