import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      redirect: '/agenda'
    },
    {
      path: '/agenda',
      name: 'agenda',
      component: () => import('@/components/Agenda.vue')
    },
    {
      path: '/token',
      name: 'token',
      component: () => import('@/components/Token.vue')
    },
    {
      path: '/own',
      name: 'own',
      component: () => import('@/components/Own.vue')
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (from.name === null && to.name !== 'agenda') {
    next('/agenda')
  } else {
    next()
  }
})

export default router