
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapGetters, mapActions } from 'vuex'

export default {
components: {
Actionsheet,
Tabbar,
TabbarItem
},
data() {
return {
show: false,
};
},
computed: {
...mapGetters(['progress', 'nav']),
menus() {
return {
...this.progress,
all: '全部'
}
}
},
methods: {
...mapActions(['setTab']),

handleClick() {
this.show = true;
},

handleClickMenu(key) {
this.setTab({
tab: key
})
}
},
};
