
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapGetters, mapActions } from "vuex";
export default {
name: "Header",
components: {
Tab,
TabItem,
},
data() {
return {
navList: ['agenda', 'token', 'own'],
tab: 0
}
},
computed: {
...mapGetters(['nav'])
},
watch: {
tab: function () {
